import React, { useState } from 'react';
import { Button, Grid } from '@material-ui/core';
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import 'fontsource-roboto';

interface FavoriteProps {
    category: 'Grammer'|'Word';
    part: string;
    id: string;
}

export default function Favorite(props:FavoriteProps) {
    const checkIsFavorite = () => {
        let favorite = JSON.parse(localStorage.getItem('favorite') ?? '{}');
        if (Array.isArray(favorite)) {
            return favorite.filter(f => f['category'] === props.category && f['id'] === props.id).length > 0;
        } else {
            return false;
        }
    }

    const [isFavorite, setIsFavorite] = useState<boolean>(checkIsFavorite());

    const setFavorite = () => {
        let favorite = JSON.parse(localStorage.getItem('favorite') ?? '{}');
        if (Array.isArray(favorite)) {
            //登録済か確認
            if (checkIsFavorite()) {
                favorite = favorite.filter(f => f['category'] !== props.category || f['id'] !== props.id);
                setIsFavorite(false);
            } else {
                favorite = favorite.concat(props);
                setIsFavorite(true);
            }
            localStorage.setItem('favorite', JSON.stringify(favorite));
        } else {
            localStorage.setItem('favorite', JSON.stringify([props]));
            setIsFavorite(true);
        }
    }


    return (
        <Grid
        container
        direction="row"
        justify="space-around"
        alignItems="flex-end"
        >
            <Button
                variant="contained"
                color="primary"
                size="small"
                onClick={setFavorite}
            >
            {
                isFavorite ? <StarIcon /> : <StarBorderIcon />
            }
            </Button>
        </Grid>
    )

}
